import React from "react"
import { Button } from "reactstrap"
import { HCF, HUtils } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import styled from "styled-components"
import V from "src/helpers/V"
import { KConfigApp } from "src/helpers/K"
export const WrapCell = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  button {
    padding: 0px;
    width: 100%;
    min-height: 35px;
    font-size: 1.5rem;
  }
  .fa-chevron-circle-down,
  .fa-chevron-down {
    color: #597ea3;
  }
  .fa-chevron-circle-up,
  .fa-chevron-up {
    color: #b3b3b3;
  }
`

export default function MTCExpandMulti(props) {
  const { row } = props
  const ctc = useConfigTableCell({ props })
  const isShow = V.TableExpand_IsShowExpandItem({
    row,
    configItemExpand: ctc.moreObj,
    prefix: ctc.fieldName,
    fnListOfMyTableCell: ctc.fnList,
  })
  ctc.row[ctc.fieldName] = {} //dam bao luon load lai khi reload list, de lay row moi
  // console.log("render MTCExpandMulti:", props, isShow, ctc.moreObj)
  let _dfStyleIcon = {}
  if (isShow === true && HCF.getCF(KConfigApp.expandIconHideColor)) {
    //show thi show icon hide
    _dfStyleIcon.color = HCF.getCF(KConfigApp.expandIconHideColor)
  }
  // console.log("_dfStyleIcon:", _dfStyleIcon)
  return (
    <WrapCell className="mtc-expand_multi disabled" style={ctc.styleWrap()}>
      <Button
        color="link"
        onClick={() => {
          // console.warn("click expand:", props, ctc, row)
          V.TableExpand_ToggleExpandItem({
            row,
            configItemExpand: ctc.moreObj,
            prefix: ctc.fieldName,
            fnListOfMyTableCell: ctc.fnList,
            forceUpdateCell: () => {
              ctc.forceUpdate()
            },
          })
        }}
      >
        <i
          className={`fa ${
            isShow === true ? "fa-chevron-up" : "fa-chevron-down"
          }`}
          style={_dfStyleIcon}
        ></i>
      </Button>
    </WrapCell>
  )
}
